.main-image {
    width: 100%;
    background-color: rbg(0, 0, 0, 0.5);
    height: 60vh;
    position: relative;
}

.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-image::before {
    content: "";
    background: url("https://images.unsplash.com/photo-1607799279861-4dd421887fb3?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.main-image h1 {
    font-size: 3rem;
    font-weight: 300;
    color: whitesmoke;
}

.main-image p {
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-width: 640px) {
    .main-image h1 {
        font-size: 3rem;
    }

    .main-image p {
        font-size: 2rem;
        text-align: center;
    }

}