.blog-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.blog-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
    margin-top: 50px;
}

.blog-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
    border-radius: 5px;
    min-height: 100%;
}

.blog-title {
    color: white;
    padding: 1rem 0;
}

.blog-details p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
    max-width: 500px;
}

.line {
    margin-top: 5px;
    margin-bottom: 30px;
}

@media screen and (max-width: 740px) {
    .project-container {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }

}